  background: linear-gradient(135deg, var(--brand-cream) 0%, var(--brand-beige) 100%);
}

.process-card-enter {
  opacity: 0;
  transform: scale(0.8) translateY(50px);
}

.process-card-active {
  opacity: 1;
  transition: all 0.8s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  transform: scale(1) translateY(0);
}

.process-card-exit {
  opacity: 0;
  transition: all 0.5s cubic-bezier(0.55, 0.055, 0.675, 0.19);
  transform: scale(0.8) translateY(-50px);
}

.process-progress-bar {
  background: linear-gradient(90deg, var(--brand-brown) 0%, var(--brand-brown-light) 100%);
  box-shadow: 0 2px 10px rgba(139, 69, 19, 0.3);
}

.process-step-indicator {
  transition: all 0.3s;
}

.process-step-indicator.active {
  box-shadow: 0 0 0 2px var(--brand-brown), 0 0 20px rgba(255, 255, 255, 0.5);
  background-color: #fff;
}

.process-background-overlay {
  background: linear-gradient(135deg, #8b4513e6 0%, #a0522dbf 50%, #65432199 100%);
}

.process-scroll-container {
  scroll-snap-type: y mandatory;
  height: 100vh;
  overflow-y: scroll;
}

